import ScrollFix from '@/vendor/ScrollFix'
export default {
  data () {
    return {
      scrollFix: null
    }
  },
  beforeDestroy () {
    if (this.scrollFix) {
      this.scrollFix.destory()
    }
  },
  beforeRouteEnter (to, from, next) {
    document.title = '商户加盟'
    next()
  },
  mounted () {
    this.$Lazyload.lazyLoadHandler()
    this.$nextTick(() => {
      this.scrollFix = new ScrollFix(this.$refs.scrollWrapper)
    })
  }
}



